.headerRoot {
  height: 50px;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #212121;
  color: #FFFFFF;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-bottom: 4px solid #64d8cb;
  padding: 0 20px;
  z-index: 90;
}

.headerContent {
  margin: auto;
  width: 60%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.headerRoot nav {
  height: 100%;
}

.desktopNav {
  width: 100%;
}

@media (max-width: 499px) {
  .desktopNav {
    display: none;
  }
}


