.NavItem {
  margin: 0;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  width: auto;
  -ms-flex-align: center;
      align-items: center;
}

.NavItem a {
  color: azure;
  text-decoration: none;
  height: 100%;
  padding: 16px 20px;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}

.NavItem a:hover,
.NavItem a:active,
.NavItem a.active {
  color: #ff5722;
}  


